import React, { Component } from 'react';

class TodoList extends Component {
  state = {
    tasks: [
      { id: 1, text: '', completed: false },
      { id: 2, text: ' ', completed: true },
      { id: 3, text: 'To do list ', completed: true },
    ],
    newTaskText: '',
  };





  handleInputChange = (event) => {
    this.setState({ newTaskText: event.target.value });
  };

  handleAddTask = () => {
    const { newTaskText, tasks } = this.state;
    if (newTaskText.trim() === '') {
      return;
    }

    const newTask = {
      id: tasks.length + 1,
      text: newTaskText,
      completed: false,
    };

    this.setState({
      tasks: [...tasks, newTask],
      newTaskText: '',
    });
  };




  handleCompleteTask = (taskId) => {
    const updatedTasks = this.state.tasks.map((task) =>
      task.id === taskId ? { ...task, completed: true } : task
    );
    this.setState({ tasks: updatedTasks });
  };


  handleIncompleteTask = (taskId) => {
    const updatedTasks = this.state.tasks.map((task) =>
      task.id === taskId ? { ...task, completed: false } : task
    );
    this.setState({ tasks: updatedTasks });
  };

  handleDeleteTask = (taskId) => {
    const updatedTasks = this.state.tasks.filter((task) => task.id !== taskId);
    this.setState({ tasks: updatedTasks });
  };







  
  render() {
    const { tasks, newTaskText } = this.state;


    return (
      <div className='workscont'>
        <h1>  </h1>
        <div className="input-container">
          <input
            type="text"
            value={newTaskText}
            onChange={this.handleInputChange}
            placeholder=" "
          />
          <button style={{color: 'white', backgroundColor : 'green'}} onClick={this.handleAddTask}></button>
        </div>
       
<div className='worklists'>
        <div className='list'>
          <h2> :</h2>
          <ul>
            {tasks
              .filter((task) => !task.completed)
              .map((task) => (
                <li style={{padding:'5px'}} key={task.id}>
                  {task.text}{' '}
                  <button style={{cursor:'pointer', backgroundColor: 'green', color: 'white', border: 'none', padding: '5px 10px' }} onClick={() => this.handleCompleteTask(task.id)}></button>{' '}
                  <button style={{cursor:'pointer', backgroundColor: 'red', color: 'white', border: 'none', padding: '5px 10px' }}onClick={() => this.handleDeleteTask(task.id)}></button>
                </li>
              ))}
          </ul>
        </div>



        <div className='list'>
          <h2> :</h2>
          <ul>
            {tasks
              .filter((task) => task.completed)
              .map((task) => (
                <li style={{padding:'5px'}} key={task.id}>
                  {task.text}{' '}
                  <button style={{ cursor:'pointer', backgroundColor: 'black', color: 'white', border: 'none', padding: '5px 10px' }} onClick={() => this.handleIncompleteTask(task.id)}></button>  {' '}      
                  <button style={{ cursor:'pointer', backgroundColor: 'red', color: 'white', border: 'none', padding: '5px 10px' }}onClick={() => this.handleDeleteTask(task.id)}></button>
                          </li>))}
          </ul>
        </div>

        </div>


      </div>
    );
  }
}

export default TodoList;